import React, { useState, useEffect } from "react";
import axios from "axios";
import Title from "../componentss/atoms/Title";
import CurrentTask from "@/componentss/CurrentTask";
import CreateTask from "@/componentss/CreateTask";

const QueueSystem = () => {
  const [tasks, setTasks] = useState([]);
  const [taskName, setTaskName] = useState("");

  // Fetch tasks from the server
  const fetchTasks = async () => {
    try {
      const response = await axios.get(`/api/task`);
      setTasks(response.data); // Assuming the response data is an array of tasks
    } catch (error) {
      console.error("Error fetching tasks:", error);
    }
  };

  useEffect(() => {
    fetchTasks();
  }, []);

  // Mark the current task as completed
  const completeTask = async () => {
    if (tasks.length > 0) {
      const [currentTask, ...remainingTasks] = tasks;
      try {
        await axios.put(`/api/task/${currentTask._id}`, {
          completed: true,
        });
        setTasks(remainingTasks); // Remove the completed task locally
      } catch (error) {
        console.error("Error completing task:", error);
      }
    }
  };

  return (
    <div className="p-8 max-w-md mx-auto bg-white rounded-lg shadow-lg space-y-5">
      <Title />
      {/* Task Creation */}
      <CreateTask
        taskName={taskName}
        setTaskName={setTaskName}
        tasks={tasks}
        setTasks={setTasks}
      />
      {/* Current Task */}
      <div className="mb-6">
        {tasks.length > 0 ? (
          <CurrentTask task={tasks[0]} completeTask={completeTask} />
        ) : (
          <p className="text-gray-500">No tasks</p>
        )}
      </div>
      {/* Remaining Tasks */}
      <div>
        {tasks.length > 1 && (
          <div>
            <h2 className="text-lg font-medium text-gray-800 mb-3">
              Remaining Tasks:
            </h2>
            <ul className="space-y-3">
              {tasks.slice(1).map((task, index) => (
                <li
                  key={task._id || index}
                  className="p-3 bg-gray-100 rounded-lg text-gray-700 border border-gray-300"
                >
                  {task.name}
                </li>
              ))}
            </ul>
          </div>
        )}
        {tasks.length <= 1 && (
          <p className="text-gray-500 mt-3">No remaining tasks</p>
        )}
      </div>
    </div>
  );
};

export default QueueSystem;
